<template>
  <div class="main">
    <ahead class='ahead' style="display:flex">
      <el-row style=" width:400px">
        <el-col :span="7">
        <el-radio-group size="medium" v-model="editmode" style="padding:20px 10p;width:100px">
          <el-radio  label="points"></el-radio>
          <el-radio  label="faces"></el-radio>
        </el-radio-group>
        </el-col>
        <el-button size="mini" @click="clear_cache">clear</el-button>
        <el-button size="mini" @click="save_to_verts">save points</el-button>
        <el-button size="mini" @click="save_to_faces">save faces</el-button>
    </el-row>
  </ahead>
  <container style="display:flex">
    <aside class="aside">
      
      <div class="aside_pts" v-show="editmode=='points'">
        <el-row class="row" v-for="pt,idx in geoInfo.pts" v-bind:key="idx">
          <el-col :span="2">
            <span>{{idx}}</span>
          </el-col>
          <el-col :span="19">
            <span style="color:#871F78">{{pt.x+','+pt.y}}</span>
          </el-col>
          <el-col :span="3">
            <el-button icon="el-icon-delete" size="mini" circle @click="hdl_pts_del(idx)"></el-button>
          </el-col>
          <!-- <el-col :span="2">
            <el-button icon="el-icon-watermelon" size="mini" circle @click="hdl_pts_high(idx)"></el-button>
          </el-col> -->
        </el-row>
      </div>
      <div class="aside_pts" v-show="editmode=='faces'">
        <el-row class="row" v-for="pt,idx in geoInfo.ploygons" v-bind:key="idx">
          <el-col :span="2">
            <span>{{idx}}</span>
          </el-col>
          <el-col :span="19">
            <span style="color:#871F78">{{pt.join(',')}}</span>
          </el-col>
          <el-col :span="3">
            <el-button icon="el-icon-delete" size="mini" circle @click="hdl_faces_del(idx)"></el-button>
          </el-col>
          <!-- <el-col :span="3">
            <el-button icon="el-icon-watermelon" size="mini" circle @click="hdl_faces_high(idx)"></el-button>
          </el-col> -->
        </el-row>
      </div>
    </aside>

    <content class="content">
   
      <el-row>
        file name:
        <span style="color:red">{{this.img.fname}}</span>
      </el-row>
      <el-row class="info">
        <el-col :span="4">current location</el-col>
        <el-col :span="2">
          x:
          <span style="color:red">{{mouse.x}}</span>
        </el-col>
        <el-col :span="3">
          y:
          <span style="color:red">{{mouse.y}}</span>
        </el-col>
        <el-col :span="9">
          closest point:
          <span style="color:red">{{getNearstPt}}</span>
        </el-col>
      </el-row>
      <div
        class="imgshow"
        @dragover="hdl_dragover_pic"
        @drop="hdl_drop_pic"
        :style="{height:imgHei,width:imgWid}"
      >
        <canvas
          class="imgshow_canvas"
          ref="canvas1"
          id="canvas1"
          @mousemove="hdl_canvas_move"
          @mousedown="hdl_canvas_down"
          @contextmenu.prevent
        ></canvas>
      </div>
    </content>
    </container>
  </div>
</template>
<script>
export default {
  name: "app",
  data() {
    return {
      mouse: { x: 0, y: 0 },
      editmode: "points",
      img: {
        url: "",
        fname: "xxx",
        height: 600,
        width: 600
      },
      geoInfo: {
        pts: [
          { x: 100, y: 40, pic: false },
          { x: 200, y: 400, pic: false },
          { x: 300, y: 20, pic: false },
          { x: 350, y: 120, pic: false },
          { x: 400, y: 20, pic: false },
          { x: 300, y: 300, pic: false },
          { x: 550, y: 400, pic: false },
          { x: 550, y: 100, pic: false }
        ],
        ploygons: [
          [0, 1, 2, 0],
          [0, 1, 4, 3, 0]
        ]
      },
      backPic: null,
      dom_canvas: null,
      ctx: null,
      dom_thnumbnail: null,
      image: null,
      nearstPt: [],
      drawInfo: {
        mouseradius: 10,
        overmax: 1000
      },
      styles: {
        pt: {
          active: "rgba(150,240,99,0.7)",
          default: "rgba(255,0,0,0.8)"
        },
        line: {
          default: "rgba(rgba(28,229,213,0.75))",
          active: "rgba(250,133,199,0.7)"
        }
      },
      cacheLines: [4, 3, 5]
    };
  },
  mounted() {
    this.image = new Image();
    this.image.onload = this.imageonload;
    this.clear_cache();
  },
  created() {
    let _this = this;
    this.$nextTick(() => {
      _this.dom_canvas = _this.$refs.canvas1;
      _this.ctx = _this.dom_canvas.getContext("2d");
      _this.drawCanvas();
    });
  },
  watch: {
    editmode: {
      handler: function(val, oldval) {
        this.drawCanvas();
      }
    },
    mouse: {
      handler: function(val, oldval) {
        this.drawCanvas();
      },
      deep: true
    },
    geoInfo: {
      handler: function(val, oldval) {
        this.drawCanvas();
      },
      deep: true
    }
  },
  methods: {
    exportRaw(name, data) {
      var urlObject = window.URL || window.webkitURL || window;
      var export_blob = new Blob([data]);
      var save_link = document.createElementNS(
        "http://www.w3.org/1999/xhtml",
        "a"
      );
      save_link.href = urlObject.createObjectURL(export_blob);
      save_link.download = name;
      this.fakeClick(save_link);
    },
    fakeClick(obj) {
      var ev = document.createEvent("MouseEvents");
      ev.initMouseEvent(
        "click",
        true,
        false,
        window,
        0,
        0,
        0,
        0,
        0,
        false,
        false,
        false,
        false,
        0,
        null
      );
      obj.dispatchEvent(ev);
    },
    clear_cache() {
      this.geoInfo.pts = [];
      this.geoInfo.ploygons = [];
      this.cacheLines = [];
    },
    save_to_verts() {
      let txt = this.geoInfo.pts.reduce((pre, next) => {
        pre += next.x + " " + next.y + "\n";
        return pre;
      }, "");
      this.exportRaw(this.img.fname + ".verts", txt);
    },
    save_to_faces() {
      let txt = this.geoInfo.ploygons.reduce((pre, next) => {
        pre += next.join(", ") + "\n";
        return pre;
      }, "");
      this.exportRaw(this.img.fname + ".faces", txt);
    },
    imageonload() {
      this.img.height = this.image.height;
      this.img.width = this.image.width;
      document.getElementById("canvas1").setAttribute("width", this.img.width);
      document
        .getElementById("canvas1")
        .setAttribute("height", this.img.height);
      this.ctx.drawImage(this.image, 0, 0, this.img.width, this.img.height);
      this.backPic = this.ctx.getImageData(
        0,
        0,
        this.img.width,
        this.img.height
      );
      this.drawCanvas();
    },
    drawCanvas() {
      this.ctx.clearRect(0, 0, this.img.width, this.img.height);
      if (this.backPic) {
        this.ctx.putImageData(this.backPic, 0, 0);
      }
      this.drawPts();
      this.editmode === "points" ? null : this.drawPloygons();
      this.drawMouseRect();
    },
    drawPts() {
      this.ctx.font = "15px Arial";
      this.geoInfo.pts.map((pt, idx) => {
        let style =
          idx === this.getNearstPt
            ? this.styles.pt.active
            : this.styles.pt.default;
        this.ctx.fillStyle = style;
        this.ctx.fillText(idx, pt.x + 5, pt.y + 4);

        this.drawPt(pt, style);
      });
    },
    drawPt(pt, style) {
      let radius = 5;
      let pt1 = { x: pt.x - radius, y: pt.y - radius };
      let pt2 = { x: pt.x + radius, y: pt.y + radius };
      let pt3 = { x: pt1.x, y: pt2.y };
      let pt4 = { x: pt2.x, y: pt1.y };
      this.drawLine(pt1, pt2, style);
      this.drawLine(pt3, pt4, style);
      this.ctx.rect(pt.x - 2, pt.y - 2, 4, 4);
      this.ctx.stroke();
    },
    drawLine(pt1, pt2, style) {
      this.ctx.lineWidth = 2;
      this.ctx.fillStyle = style;
      this.ctx.beginPath();
      this.ctx.moveTo(pt1.x, pt1.y);
      this.ctx.lineTo(pt2.x, pt2.y);
      this.ctx.closePath();
      this.ctx.strokeStyle = style;
      this.ctx.stroke();
    },
    drawPloygons() {
      this.geoInfo.ploygons.map(ploygon => {
        let geo = ploygon.reduce((pre, next) => {
          pre.push(this.geoInfo.pts[next]);
          return pre;
        }, []);
        this.drawPloygon(geo, this.styles.line.default);
      });
      let geo = this.cacheLines.reduce((pre, next) => {
        pre.push(this.geoInfo.pts[next]);
        return pre;
      }, []);
      this.drawPloygon(geo, this.styles.line.active);
    },
    drawPloygon: function(ploygon, style) {
      for (let i = 0; i < ploygon.length - 1; i++) {
        this.drawLine(ploygon[i], ploygon[i + 1], style);
      }
    },
    drawMouseRect: function() {
      let radius = this.drawInfo.mouseradius;
      this.ctx.beginPath();
      this.ctx.rect(
        this.mouse.x - radius,
        this.mouse.y - radius,
        radius * 2,
        radius * 2
      );
      this.ctx.closePath();
      this.ctx.stroke();
      this.drawPt(this.mouse, this.styles.pt.default);
    },
    hdl_pts_del: function(idx) {
      this.geoInfo.pts.splice(idx, 1);
    },

    hdl_faces_del: function(idx) {
      this.geoInfo.ploygons.splice(idx, 1);
    },
    hdl_dragover_pic: function(e) {
      e.stopPropagation();
      e.preventDefault();
      e.dataTransfer.dropEffect = "copy";
    },
    hdl_drop_pic: function(e) {
      e.stopPropagation();
      e.preventDefault();
      var files = e.dataTransfer.files;
      var file = files[0];
      this.img.fname = file.name;
      var fileReader = new FileReader();
      fileReader.readAsDataURL(file); //将file读为url
      let _this = this;
      fileReader.onload = function(ev) {
        // _this.$refs.image1.src = fileReader.result;
        _this.image.src = fileReader.result;
      };
    },
    hdl_canvas_move(e) {
      this.mouse = {
        x: e.clientX - this.dom_canvas.offsetLeft,
        y: e.clientY - this.dom_canvas.offsetTop
      };
    },
    hdl_canvas_down(e) {
      e.stopPropagation();
      e.preventDefault();
      if (this.editmode == "points") {
        if (e.button == 0) {
          //左键，有最近点则更新，没有则添加
          if (this.getNearstPt > -1) {
            this.geoInfo.pts[this.getNearstPt] = this.mouse;
          } else {
            this.geoInfo.pts.push(this.mouse);
          }
        } else if (e.button == 2) {
          //右键，有最近点则删除
          this.geoInfo.pts.splice(this.getNearstPt, 1);
        }
      } else if (this.editmode == "faces") {
        if (this.getNearstPt > -1) {
          if (e.button == 0) {
            //左键，有最近点则更新，没有则添加
            if (this.cacheLines.indexOf(this.getNearstPt) == -1) {
              //新的点
              this.cacheLines.push(this.getNearstPt);
            } else if (this.cacheLines.indexOf(this.getNearstPt) == 0) {
              //点在首部，形成闭环
              if (this.cacheLines.length > 2) {
                this.cacheLines.push(this.getNearstPt);
                this.geoInfo.ploygons.push([...this.cacheLines]);
                this.cacheLines = [];
              }
            }
          } else if (e.button == 2) {
            //右键，有最近点则删除
            this.cacheLines.splice(this.cacheLines.indexOf(this.getNearstPt));
          }
        }
      }
    },
    ptInRect: function(pt, center, radius) {
      return pt.x > center.x - radius &&
        pt.x < center.x + radius &&
        pt.y > center.y - radius &&
        pt.y < center.y + radius
        ? true
        : false;
    }
  },
  computed: {
    imgHei: function() {
      return this.img.height + "px";
    },
    imgWid: function() {
      return this.img.width + "px";
    },
    getNearstPt: function() {
      let radius = this.drawInfo.mouseradius;
      let overmax = this.drawInfo.overmax;
      let lens = [];
      let _this = this;
      this.geoInfo.pts.forEach(pt => {
        if (!_this.ptInRect(_this.mouse, pt, radius)) {
          lens.push(overmax);
        } else {
          let deltaX = _this.mouse.x - pt.x;
          deltaX *= deltaX;
          let deltaY = _this.mouse.y - pt.y;
          deltaY *= deltaY;
          lens.push(deltaY + deltaX);
        }
      });
      return Math.min(...lens) < overmax ? lens.indexOf(Math.min(...lens)) : -1;
    }
  }
};
</script>







<style lang="scss">

.main {
  color: rgb(0, 0, 0);
  margin-top: 0px;

  .ahead{
  width: 100%
}
  .aside {
    width: 300px;
    height: 700px;
    margin: 3px;
    overflow: scroll;
    // border: 1px solid gray;
    // background-color: #c4cce7;
    .toolbar {
      // background-color: rgba(206, 106, 106, 0.2);
      display: flex;
      justify-content: space-around;
    }
    .contain_pts {
      // background-color: rgba(102, 166, 215, 0.2);
      border: 1px solid #2e3037;
      width: 300px;
      padding: 0px;
      .row {
        display: flex;
        justify-content: space-between;
        align-content: center;
      }
    }
  }
  .content {
    float: left;
    .info {
      width: 800px;
      height: 30px;
    }
    .imgshow {
      border: 1px solid #2e3037;
    }
